@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;1,400;1,700&display=swap);
body {
    font-family: 'PT Sans', sans-serif;
    font-size: 15px;
    background-color: #F8F8F8;
    margin: 10px 20px 10px;
}

header input {
    width: 8rem;
    height: 2rem;
    margin-bottom: 0.5rem;
    margin-top: 0.2rem;
    background-color: transparent;
    border: none;
    border-bottom-style: solid;
    border-bottom-color: #F8F8F8;
    color: #F8F8F8;
}

header input:focus, footer input:focus {
    outline: 0;
}

header label, header p {
    color: #F8F8F8;
    font-style: italic;
    margin-top: 1rem;
}

header, footer {
    background-color: #4c4c4c;
}

header button:hover {
    background: #dcc5a1;
}

footer input {
    background-color: transparent;
    color: #F8F8F8;
    border: none;
    border-bottom-style: solid;
    border-bottom-color: #F8F8F8;
}

footer a:hover{
    background-color: inherit;
}

.calendar {
    font-style: normal;
}

/*========================================================*/

.cursor {
    cursor: pointer;
}

.btn {
    color: #F8F8F8;
}
.colorBtnLang {
    background-color: #dcc5a1;
    color: #4c4c4c;
}
.react-datepicker__day--selected,
.react-datepicker__day--in-selecting-range,
.react-datepicker__day--in-range,
.react-datepicker__month-text--selected,
.react-datepicker__month-text--in-selecting-range,
.react-datepicker__month-text--in-range,
.react-datepicker__quarter-text--selected,
.react-datepicker__quarter-text--in-selecting-range,
.react-datepicker__quarter-text--in-range,
.react-datepicker__year-text--selected,
.react-datepicker__year-text--in-selecting-range,
.react-datepicker__year-text--in-range {
    background-color:#dcc5a1 !important;
}
.react-datepicker__day--keyboard-selected,
.react-datepicker__month-text--keyboard-selected,
.react-datepicker__quarter-text--keyboard-selected,
.react-datepicker__year-text--keyboard-selected {
    background-color: #dcc5a1 !important;
}

.close {
    position: absolute;
    right: 25px;
    top: 0;
    color: #000;
    font-size: 25px;
}

.close:hover,
.close:focus {
    color: red;
    cursor: pointer;
}
.active {
    border: 1px solid #888;
    color: #888;
    pointer-events: none;
}

.disabled {
    pointer-events: none;
    box-shadow: none;
    color: #999;
}


li::marker{
    font-size: 0.5rem;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type='number'] {
    -moz-appearance: textfield;
}

ol {
    list-style: none;
    counter-reset: li;
}

ol li:before {
    counter-increment: li;
    content: counters(li, ".") ". ";
}



